<template>
  <div class="cart-family-bundle__undo">
    <button
        class="cart-family-bundle__undo-button text-link"
        tabindex="0"
        @click.prevent="handleUndoCart()"
      >
        <span v-text="'Undo Bundle Upgrade'" />
      </button>
  </div>
</template>

<script>

/**
 * Vue: Cart Bundle Slide (cart-family-bundle-slide)
 * -----------------------------------------------------------------------------
 * Newsletter form.
 *
 */

import { mapActions } from 'vuex'
import timings from '~/config/timings'

export default {
  name: 'CartFamilyBundleSlide',

  props: {
    lineItem: {
      type: Object,
      default: () => ({}),
    },
  },

  data() {
    return {
    }
  },

  methods: {

    /**
     * Map Vuex actions.
     */
    ...mapActions({
      addItem: 'cart/addItem',
    }),

    /**
     * Handle undo cart submission.
     */
    async handleUndoCart() {
      clearTimeout(this.timeout)
      this.error = ''
      this.state = 'adding'
      cnvs.EventBus.emit('family-bundle-updating')

      if (cnvs.storybook) {
        this.timeout = setTimeout(() => {
          this.state = 'added'
        }, timings.slow)

        this.timeout = setTimeout(() => {
          this.state = 'ready'
        }, timings.message)

        return
      }

      const {_familyBundle, _lowestPrice, _recommendedPrice, _undoId} = this.lineItem.properties._undoProperties
      const properties = { _familyBundle, _lowestPrice, _recommendedPrice }

      const items = [
        {
          id: _undoId,
          properties,
          quantity: this.lineItem.quantity,
        },
      ]

      try {
        await this.addItem(items)

        this.state = 'added'

        cnvs.EventBus.emit('family-bundle-remove', {key: this.lineItem.key})

        this.timeout = setTimeout(() => {
          this.state = 'ready'
        }, timings.message)

      } catch (error) {
        this.state = 'error'

        if (error.length) {
          this.error = error
          return
        }

        reportError('Failed to add product to cart', error)
      }
    },
  },
}
</script>
