<template>
  <div
    class="line-item"
    :class="[
      $blockClass(blockClass, 'line-item'),
      {
        [$class('loading')]: !item,
        [$class('updating')]: state === 'updating'
      },
    ]"
  >
    <div class="line-item__wrapper">
      <a :href="item.url">
        <responsive-image
          :class="$blockClass(blockClass, 'line-item-image')"
          block-class="line-item"
          :image="item.featured_image"
          :lazy-load="true"
          min-max="125-205"
          :padding="false"
          :style="{ animationDelay: animationDelay }"
        />
      </a>

      <div
        class="line-item__body"
        :class="$blockClass(blockClass, 'line-item-body')"
        :style="{ animationDelay: animationDelay }"
      >
        <a
          class="line-item__title subtitle-2"
          :class="$blockClass(blockClass, 'line-item-title')"
          :href="item.url"
          v-text="item.product_title"
        />

        <p
          v-if="item.variant_title !== item.product_title"
          class="line-item__variant body-2"
          :class="$blockClass(blockClass, 'line-item-variant')"
          v-text="item.variant_title"
        />

        <p
          v-if="item.properties?.ecoTax"
          class="line-item__variant body-2"
          v-text="item.properties.ecoTax"
        />

        <product-price-message
          v-if="$variable('settings.pricingDirective.enable')"
          :lowest-price="item.properties?._lowestPrice || false"
          :recommended-price="compareAtPrice > item.original_price ? $formatMoney(compareAtPrice) : false"
        />

        <product-prices
          block-class="line-item"
          class="body-2"
          :class="$blockClass(blockClass, 'line-item-prices')"
          :compare-at-price="compareAtPrice"
          :price="item?.final_price"
        />

        <span
          v-if="item?.properties?._bundleAddOn"
          v-text="$string('product_bundle_item.promo_note')"
        />

        <div
          class="line-item__footer"
          :class="$blockClass(blockClass, 'line-item-footer')"
        >
          <quantity-selector
            v-if="!item.properties?._buyxgety"
            :class="$blockClass(blockClass, 'line-item-quantity')"
            block-class="line-item"
            :item="item"
            :quantity="quantity"
            :state="state"
            @update-quantity="handleQuantityUpdate"
          />

          <p
            v-else
            class="cart-bundle__drawer-item"
          >
            <span v-text="bundleMessage" />
            <br>
            <span v-text="bundleQuantity" />
          </p>

          <button
            class="line-item__remove icon-button"
            :class="[
              $blockClass(blockClass, 'line-item-remove'),
              { [$class('disabled')]: state === 'updating' },
            ]"
            type="button"
            :aria-label="$string('cart.line_item.remove')"
            @click="handleRemove()"
          >
            <icon-remove />
          </button>
        </div>
      </div>
    </div>

    <cart-family-bundle
      v-if="familyBundle && familyBundle?.length > 0"
      :bundles="familyBundle"
      :line-item="item"
      :index="index"
    />

    <cart-family-bundle-undo
      v-if="item.properties?._undoProperties"
      :line-item="item"
    />
  </div>
</template>

<script>

/**
 * Vue: Line item (line-item)
 * -----------------------------------------------------------------------------
 * Displays line items in cart, cart drawer, and order.
 *
 * @param {String} [blockClass] - Block level class to apply to all elements.
 * @param {Number} [index] - Line item's forloop index.
 * @param {Boolean|Object} item - Line item in cart response or order.
 *
 */
import { mapActions, mapState } from 'vuex'
import CartFamilyBundle from '~async/cart-family-bundle/cart-family-bundle'
import CartFamilyBundleUndo from '~async/cart-family-bundle/cart-family-bundle-undo'
import ProductPrices from '~global/product-prices/product-prices'
import QuantitySelector from '~async/quantity-selector/quantity-selector'
import ResponsiveImage from '~global/images/responsive-image'
import ProductPriceMessage from '~global/product-price-message/product-price-message'

import IconRemove from '~icons/misc/remove.svg'

import { values as timings } from '~/config/timings'
import { addToCartEvent, removeFromCartEvent } from '~/helpers/datalayer'
import productsByQuery from '~/graphql/queries/productsByQuery.gql'
import { formatProduct } from '~/helpers/format'

export default {
  name: 'LineItem',

  components: {
    CartFamilyBundle,
    CartFamilyBundleUndo,
    IconRemove,
    ProductPrices,
    QuantitySelector,
    ProductPriceMessage,
    ResponsiveImage,
  },

  props: {
    blockClass: {
      type: String,
      default: '',
    },
    bundles: {
      type: Array,
      default: () => ([]),
    },
    bundleDiscount: {
      type: [Boolean, Number],
      default: false,
    },
    index: {
      type: Number,
      default: 0,
    },
    item: {
      type: [Boolean, Object],
      default: false,
      required: true,
    },
  },

  data() {
    return {
      familyBundle: [],
      quantity: 1,
      state: 'loading',
      bundleMessage: window.cnvs.productBundles?.cartLineMessage || false,
    }
  },

  computed: {

    /**
     * Map Vuex state.
     */
    ...mapState({
      cartStatus: (state) => state.cart.status,
    }),

    /**
     * Compute animation delay.
     * @returns {String}
     */
    animationDelay() {
      return `${this.index * timings.quick}s`
    },

    /**
     * Bundle quantity text
     * @returns {String}
     */
    bundleQuantity() {
      return `${this.$string('cart.label.bundle_quantity')}: ${this.quantity}`
    },

    /**
     * Get compare at price for line items
     * @returns {Number}
     */
    compareAtPrice() {
      if (!this.item) {
        return
      }

      return this.bundleDiscount ? this.item.original_price : this.item.compare_at_line_price
    },

    hasPricingMessage() {
      const enabled = this.$variable('settings.pricingDirective.enable')

      if (!enabled) {
        return false
      }

      return this.compareAtPrice > this.item.original_price || this.properties?._lowestPrice
    },

    familyBundleName() {
      return this.item.properties?._familyBundle ? this.item.properties._familyBundle : ''
    },
  },

  watch: {

    /**
     * Watch cart status items to see if item is being updated.
     */
    'cartStatus.items': function() {
      if (this.cartStatus.items.includes(this.item.key)) {
        this.state = 'updating'
        return
      }

      this.state = 'ready'
    },

    /**
     * Watch item quantity to update quantity if updated elsewhere.
     */
    'item.quantity': function() {
      this.quantity = this.item.quantity
    },
  },

  mounted() {
    this.state = 'ready'
    this.quantity = this.item.quantity

    if (this.item.properties?._familyBundle) {
      this.queryFamilyProducts(this.item.properties._familyBundle)
    }

    this.setEventListeners()
  },

  methods: {

    /**
     * Map Vuex actions.
     */
    ...mapActions({
      updateItem: 'cart/updateItem',
      removeItem: 'cart/updateItem',
    }),

    /**
     * Set event listeners
     */
    setEventListeners() {
      cnvs.EventBus.listen('family-bundle-remove', (remove) => {
        if (this.item.key === remove.key) {
          this.handleRemove()
        }
      })
    },

    /**
     * Handle quantity update event.
     * @param {String} quantity - New quantity.
     */
    async handleQuantityUpdate(quantity) {
      const initialQuantitiy = this.item.quantity
      this.quantity = quantity

      try {
        this.state = 'updating'

        await this.updateItem([
          {
            id: this.item.key,
            quantity: this.quantity,
          },
        ])

        this.state = 'ready'

        this.quantity > initialQuantitiy
        ? addToCartEvent(this.item)
        : removeFromCartEvent(this.item, 1)

      } catch (error) {
        this.quantity = this.item.quantity
        this.state = 'error'
      }
    },

    /**
     * Load products by tag from GraphQL.
     */
     async queryFamilyProducts(tag) {
      const family = tag

      if (!family) {
        return
      }

      const query = `tag:'product-family:${family}'`

      try {
        const response = await cnvs.Query({
          locale: cnvs.store.language,
          query: productsByQuery,
          variables: {
            country: cnvs.store.country,
            query,
          },
        })

        if (
          cnvs.settings.disableComponentReadyState ||
          !response.products.edges.length
        ) {
          return
        }

        const filterProducts = response.products.edges.filter((product) => {
          return this.item.handle !== product.node.handle
        })

        this.familyBundle = filterProducts.map((node) => {
          return formatProduct({ product: Object.values(node)[0] })
        })


      } catch (error) {
        reportError('Failed to load family products', error)
      }
    },

    /**
     * Handle remove click.
     */
    handleRemove() {
      if (this.state === 'updating') {
        return
      }
      removeFromCartEvent(this.item, this.item.quantity)
      this.removeItem(this.item.key)
    },
  },
}
</script>

<style lang="scss">
@import './line-item';
</style>
